import Axios from "../../../api/Axios";
import Form from "../../../components/Form/index.tsx";
import Select from "../../../components/Select/index.tsx";
import PicUpload from "../../../components/PicUpload/index.tsx";
import { BtnWrapper, Btn, BtnSecondary, Column as Col, Row } from "../../../components/StyledComponent/index.tsx";
import { useState, useRef, useEffect, useCallback } from "react";
import Loader from "../../../components/Loader";
import { toast } from "react-toastify";
import { useNavigate, useParams } from "react-router-dom";
import triggerSwalAlert from "../../../helpers/triggerSwalAlert";
import TextInput from "../../../components/TextInput/index.tsx";

const axios = new Axios();

const AddEditBanner = (props) => {

  const [pending, setPending] = useState(false);
  const navigate = useNavigate();
  const [details, setDetails] = useState({type: "", imageJson: {desktop: {}, tab: {}, mobile: {}}})
  const { id } = useParams();
  const formRef = useRef();

  const getBannerDetails = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi(`/banner/getBannerDetails/${id ? id : ""}`, options);
    if (result?.status == 200) {
      setDetails(result?.data?.data)
      setPending(false)
    }
    else {
      setPending(false)
      toast.error("Something went wrong while fetching product")
    }
  }, [])

  useEffect(() => {
    if(props.type == "edit"){
      getBannerDetails()
    }
  }, [])

  const handleBannerImageChange = async (value, type) => {
    let formData = new FormData();
    formData.append("productImage", value);
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData,
      onUploadProgress: (data) => {
        details.imageJson[type].progress = Math.round((100 * data.loaded) / data.total);
        setDetails(JSON.parse(JSON.stringify(details)))
      },
    }
    let result = await axios.callApi("/file/uploadFile", options)

    if (result.status == 200) {
      details.imageJson[type].image = result?.data?.data
      setDetails(JSON.parse(JSON.stringify(details)))
    }
    else {
      details.imageJson[type].progress = -1
      setDetails(JSON.parse(JSON.stringify(details)))
    }
  };
  const handleBannerImageDelete = async (type) => {
    setPending(true);
    let selectedImage = details?.imageJson?.[type]?.image
    let splitImage = selectedImage.split("/")
    let imageName = splitImage[splitImage.length - 1]
    const options = {
      method: "DELETE",
      body: {
        name: imageName
      }
    }
    let result = await axios.callApi(`/file/deleteFile`, options)
    if (result?.status === 200) {
      setPending(false);
      details.imageJson[type].image = ""
      details.imageJson[type].progress = 0
      setDetails(JSON.parse(JSON.stringify(details)))
      return "success";
    }
    else {
      setPending(false);
      toast.error("Error in deleting image. Something went wrong")
      return "failed"
    }
  };

  const submit = async (value) => {
    setPending(true);
    const {
     type,
     pageLink
    } = value;

    const payload = {
      type,
      pageLink: pageLink.replace(process.env.REACT_APP_FRONTEND_URL,""),
      imageJson: details.imageJson
    }

    if(props.type == "edit"){
      payload["id"] = details.id
    }

    let options = {
      method: (props.type == "edit") ? "PUT" : "POST",
      body: payload
    }

    let url = (props.type == "edit") ? "/banner/updatebanner" : "/banner/createBanner"

    let result = await axios.callApi(url, options);

    if (result?.status === 200) {
      let message = (props.type == "edit") ? "Updated successfully" : "Banner successfully created"
      triggerSwalAlert("Success", message, "success" )
      setPending(false);
    } else {
      triggerSwalAlert("Error", "Something went wrong", "error" )
      setPending(false);
    }

    navigate("/manage-banners")

  };
  return (
    <>
      {pending ? <Loader /> : null}
      <div className="container-fluid addEditProductContainer my-lg-5">
        <Form onSubmit={submit} ref={formRef}>
          <Row>
            <Col md={6}>
              <Select
                name="type"
                label="Choose Banner Type"
                //imageSrc={Time}
                required
                isFormInputElement
                value={details?.type}
                data={[{
                  "value": "topBanner",
                  "label": "Top Banner"
                },
                {
                  "value": "justLaunched",
                  "label": "Just Launched"
                }]}
              />
            </Col>
            <Col md={6}>
              <TextInput
                name="pageLink"
                label="Enter Page Link"
                //imageSrc={Time}
                required
                isFormInputElement
                value={details?.pageLink}
              />
            </Col>
            <Col md={12}>
              <PicUpload
                name={`desktopImage`}
                label="Banner Desktop Image/Gif"
                //imageSrc={image}
                isFormInputElement
                required
                acceptedFormat="image/png, image/jpeg, image/jpg, image/gif"
                onChange={(value)=>handleBannerImageChange(value, "desktop")}
                value={details?.imageJson?.desktop?.image ? process.env.REACT_APP_BASE_URL + "/" + details?.imageJson?.desktop?.image : ""}
                deleteFile={()=>handleBannerImageDelete("desktop")}
                progress={details?.imageJson?.desktop?.progress}
              />
            </Col>
            <Col md={12}>
              <PicUpload
                name={`tabImage`}
                label="Banner Tab Image/Gif"
                //imageSrc={image}
                isFormInputElement
                required
                acceptedFormat="image/png, image/jpeg, image/jpg, image/gif"
                onChange={(value)=>handleBannerImageChange(value, "tab")}
                value={details?.imageJson?.tab?.image ? process.env.REACT_APP_BASE_URL + "/" + details?.imageJson?.tab?.image : ""}
                deleteFile={()=>handleBannerImageDelete("tab")}
                progress={details?.imageJson?.tab?.progress}
              />
            </Col>
            <Col md={12}>
              <PicUpload
                name={`mobileImage`}
                label="Banner Mobile Image/Gif"
                //imageSrc={image}
                isFormInputElement
                required
                acceptedFormat="image/png, image/jpeg, image/jpg, image/gif"
                onChange={(value)=>handleBannerImageChange(value, "mobile")}
                value={details?.imageJson?.mobile?.image ? process.env.REACT_APP_BASE_URL + "/" + details?.imageJson?.mobile?.image : ""}
                deleteFile={()=>handleBannerImageDelete("mobile")}
                progress={details?.imageJson?.mobile?.progress}
              />
            </Col>
          </Row>
          <BtnWrapper>
            <div>
              <BtnSecondary
                style={{ marginRight: 12 }}
              >
                Cancel
              </BtnSecondary>
              <Btn
                disabled={false}
                onClick={() => formRef.current?.handleSubmit()}
              >
                {false ? <i className="fas fa-spinner fa-pulse"></i> : "Submit"}
              </Btn>
            </div>
          </BtnWrapper>
        </Form>
      </div>
    </>
  )
}

export default AddEditBanner;